header {
	height: 100%;
	padding-top: 7rem;
	overflow: hidden;
}

.header__container {
	text-align: center;
	height: 100%;
	position: relative;
}

.cta {
	display: flex;
	justify-content: center;
	gap: 1.2rem;
	margin-top: 2.5rem;
}

.header__socials {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 0.8rem;
	position: absolute;
	left: 5rem;
	bottom: 1rem;
}

.header__socials::after {
	content: '';
	width: 1px;
	height: 2rem;
	background: var(--color-primary);
}

.scroll__down {
	position: absolute;
	right: 5rem;
	bottom: 5rem;
	transform: rotate(90deg);
	font-weight: 300;
	font-size: 0.9rem;
}

@media screen and (max-width: 1024px) {
	header {
		height: 68vh;
	}

	section {
		margin-top: 6rem;
	}
}

@media screen and (max-width: 600px) {
	header {
		height: 65vh;
	}

	.header__socials {
		display: flex;
		flex-direction: row;
		position: relative;
		justify-content: center;
		left: 0;
		bottom: 0;
		margin-top: 30px;
	}

	.scroll__down {
		display: none;
	}
}
